@import "vars";


/*==========  Desktop First  ==========*/

// 1200px
@include media-breakpoint-down(lg) {
	.header {
		&-logo {
			position: absolute;
			padding: 0;
			z-index: 1; }
		&-list {
			max-height: 0;
			overflow: hidden;
			flex-direction: column;
			transition: all .25s ease-in-out;
			&.open {
				max-height: 1000px;
				height: auto; } } }
	.panel {
		&-table {
			box-shadow: none;
			overflow: visible;
			&-head {
				display: none; }
			&-body {
				&-row {
					margin-bottom: 15px;
					display: block;
					border-radius: 20px;
					overflow: hidden;
					box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.15);
					&:nth-child(even) {
						background-color: transparent; } }
				&-item {
					display: block;
					text-align: right;
					padding: 16px 30px !important;
					border-bottom: 1px solid $gray;
					&:last-child {
						border-bottom: 0; }
					&.image {
						img {
							margin: 0 0 0 auto; }
						&:before {
							margin-top: 16px; } }
					&:before {
						content: attr(data-label);
						float: left;
						font-weight: bold;
						text-transform: uppercase; } } } } } }

// 992px
@include media-breakpoint-down(md) {
	.panel {
		&-navbar {
			justify-content: center; } }
	.cart {
		&.open {
			left: 0;
			right: 0;
			bottom: 0;
			top: 0;
			width: 100%;
			transform: none; } } }

// 768px
@include media-breakpoint-down(sm);

// 576px
@include media-breakpoint-down(xs) {
	.panel {
		&-table {
			&-body {
				&-item {
					padding: 16px !important; } } } }
	.cart {
		left: 0;
		width: 100%;
		&-total {
			flex-wrap: wrap;
			justify-content: center; } } }


/*==========  Mobile First  ==========*/

// 576px
@include media-breakpoint-up(sm);

// 768px
@include media-breakpoint-up(md);

// 992px
@include media-breakpoint-up(lg);

// 1200px
@include media-breakpoint-up(xl);
